import React, { Component } from 'react';
import Counter from './counter';

class Counters extends Component {
    

    render() { 
        
        return (  
           <div>
               <button onClick={this.props.onReset} className="btn btn-primary sm-2 m-4">Reset</button>
                {this.props.counters.map(counter=><
                Counter counter={counter}  key={counter.id} onDelete={this.props.onDelete} onIncrement={this.props.onIncrement}>
                <h4>Title {counter.id}</h4>
                </Counter>)}
           </div>
        );
    }
}
 
export default Counters;