import React, { Component } from 'react';

class Navbar extends Component {
    state = {  }
    render() { 
        return (
        <div>
            <nav className="navbar navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <h1>Total Counts: {this.props.totalCount} </h1>                    
                </a>
            </nav>
        </div>
         );
    }
}
 
export default Navbar;